import React, { Component } from 'react';
import Game from './Game';
import DraggableItem from './DraggableItem';
import Board from './js/boardData';
import Event from './js/event';

import './App.css';

class Level extends Component {
  state = {
    progress: [],
    gameStartPosition: false
  }

  componentWillMount(){
    Board("set", {board: this.props.level.boardData, words: this.props.level.words.slice(1)});
    Event.on("progress", (progress) => {
      this.setState({progress});
      if(progress.length === this.props.level.words.slice(1).length){
        setTimeout(() => {
          this.props.won();
        }, 300);
      }
    });
  }

  componentDidMount(){
    setTimeout(() => {
      this.animateGameStart();
    }, 2000);
  }

  animateGameStart(){
    this.setState({
      gameStartPosition: true
    });
  }

  getProgress(found, count){
    let wordProgressArray = [];
    let i = 1;
    while(i <= count){
      if(i <= found){
        wordProgressArray.push(<div key={i} className="found"></div>);
      }else{
        wordProgressArray.push(<div key={i} className="notfound"></div>);
      }
      i++;
    }
    return wordProgressArray;
  }

  render() {
    return (
      <div className="App">
        <div className={(!this.state.gameStartPosition?"findInfo":"findInfo findInfoStarted")}>
          <span className="smallInfoText">TILE WORDS ABOUT</span>
          <span>{this.props.level.words[0]}</span>
          <div className={(!this.state.gameStartPosition?"foundCarrier":"foundCarrier foundCarrierStarted")}>
            {this.getProgress(this.state.progress.length, this.props.level.words.length - 1)}
          </div>
        </div>
        <div className={(!this.state.gameStartPosition?"generalCarrier":"generalCarrier generalCarrierStarted")}>
          <div className="gameCarrier">
            <Game
              board={this.props.level.board}
              boardData={this.props.level.boardData}/>
          </div>
          <div className="draggableItemsHolder">
            {this.props.level.draggables.map((draggableData, i) => {
              return (<DraggableItem key={i} item={draggableData}/>);
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default Level;



