import React, { Component } from 'react';
import Level from './Level';
import Audio from './Audio';
import Tutorial from './Tutorial';
import Board from './js/boardData';
import './App.css';

import Levels from './data/levels/';

class App extends Component {
  state = {
    currentLevel: false
  }
  componentWillMount(){
    document.addEventListener("message", (event) => {
      if(event.data === "hintRequested"){
        let hintLetter = Board("hint");
        window.postMessage(hintLetter+":hint");
      }else{
        let levelInfo = JSON.parse(event.data);
        this.level = Levels[levelInfo.city.slug.replace("-", "_") + "_" + levelInfo.id+"_json"];
        this.setState({
          currentLevel: levelInfo
        });
      }
    }, false);

    setTimeout(() => {
      // if no event has come in
      if(!this.state.currentLevel){
        this.level = Levels["paris_5_json"];
        console.log(this.level);
        this.setState({
          currentLevel: {
            id: 1,
            city: {
              slug: "paris"
            }
          }
        });
      }
    }, 3000);
  }

  gameWon(){
    let message = this.state.currentLevel.city.id + "-" + this.state.currentLevel.id + ":gameWon";
    window.postMessage(message);
  }

  render() {
    if(this.state.currentLevel){
      return (
        <div className="App">
          <Level level={this.level} city={this.state.currentLevel.city} won={this.gameWon.bind(this)}/>
          <Audio/>
          {(this.state.currentLevel.id === 1 && this.state.currentLevel.city.slug === "paris" ) &&
            <Tutorial/>
          }
        </div>
      );
    }else{
      return null;
    }
  }
}

export default App;



