// Decides and creates borders for draggables

export function getBorderStyle(draggable, i, j){
  const innerBorderColor = "#fafafa";
  const borderRadius = "0px";
  const borderColor = "#ddd";
  if(draggable.length === 1){
    if(draggable[0].length === 1){
      return {border: "1px solid "+borderColor};
    }
    if(draggable[0].length === 2){
      if(j === 0){
        return {border: "1px solid "+borderColor, borderRight: "1px solid "+innerBorderColor, borderTopRightRadius: "0px", borderBottomRightRadius: "0px"};
      }else if(j === 1){
        return {border: "1px solid "+borderColor, borderLeft: "1px solid "+innerBorderColor, borderTopLeftRadius: "0px", borderBottomLeftRadius: "0px"};
      }
    }
  }else if(draggable.length === 2){
    if(draggable[0].length === 1 && draggable[1].length === 1){
      if(i === 0){
        return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderBottomLeftRadius: "0px", borderBottomRightRadius: "0px"};
      }else if(i === 1){
        return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderTopLeftRadius: "0px", borderTopRightRadius: "0px"};
      }
    }
    if(draggable[0].length === 2 && draggable[1].length === 2){
      if(draggable[0][0] !== "0" && draggable[0][1] !== "0" && draggable[1][0] !== "0" && draggable[1][1] !== "0"){
        if(i === 0){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderRight: "1px solid "+innerBorderColor, borderRadius: "0px", borderTopLeftRadius: borderRadius};
          }else{
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderLeft: "1px solid "+innerBorderColor, borderRadius: "0px", borderTopRightRadius: borderRadius};
          }
        }else if(i === 1){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderRight: "1px solid "+innerBorderColor, borderRadius: "0px", borderBottomLeftRadius: borderRadius};
          }else{
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderLeft: "1px solid "+innerBorderColor, borderRadius: "0px", borderBottomRightRadius: borderRadius};
          }
        }
      }
      if(draggable[0][0] === "0"){
        if(i === 0){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderRight: "1px solid "+innerBorderColor, borderBottomRightRadius: borderRadius};
          }else{
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderBottomLeftRadius: "0px", borderBottomRightRadius: "0px"};
          }
        }else if(i === 1){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderRight: "1px solid "+innerBorderColor, borderTopRightRadius: "0px", borderBottomRightRadius: "0px"};
          }else{
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderLeft: "1px solid "+innerBorderColor, borderRadius: "0px", borderBottomRightRadius: borderRadius};
          }
        }
      }
      if(draggable[1][0] === "0"){
        if(i === 0){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderRight: "1px solid "+innerBorderColor,  borderTopRightRadius: "0px", borderBottomRightRadius: "0px"};
          }else{
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderLeft: "1px solid "+innerBorderColor,  borderRadius: "0px", borderTopRightRadius: borderRadius};
          }
        }else if(i === 1){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderRight: "1px solid "+innerBorderColor, borderRadius: "0px", borderBottomLeftRadius: borderRadius};
          }else{
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderTopLeftRadius: "0px", borderTopRightRadius: "0px"};
          }
        }
      }
      if(draggable[0][1] === "0"){
        if(i === 0){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderBottomLeftRadius: "0px", borderBottomRightRadius: "0px"};
          }else{
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderLeft: "1px solid "+innerBorderColor, borderRadius: "0px", borderTopRightRadius: borderRadius};
          }
        }else if(i === 1){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderRight: "1px solid "+innerBorderColor, borderRadius: "0px", borderBottomLeftRadius: borderRadius};
          }else{
            return {border: "1px solid "+borderColor, borderLeft: "1px solid "+innerBorderColor, borderBottomLeftRadius: "0px", borderTopLeftRadius: "0px"};
          }
        }
      }
      if(draggable[1][1] === "0"){
        if(i === 0){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderBottom: "1px solid "+innerBorderColor, borderRight: "1px solid "+innerBorderColor, borderRadius: "0px", borderTopLeftRadius: borderRadius};
          }else{
            return {border: "1px solid "+borderColor, borderLeft: "1px solid "+innerBorderColor, borderBottomLeftRadius: "0px", borderTopLeftRadius: "0px"};
          }
        }else if(i === 1){
          if(j === 0){
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderTopLeftRadius: "0px", borderTopRightRadius: "0px"};
          }else{
            return {border: "1px solid "+borderColor, borderTop: "1px solid "+innerBorderColor, borderLeft: "1px solid "+innerBorderColor,  borderRadius: "0px", borderBottomRightRadius: borderRadius};
          }
        }
      }
    }
  }
}


export function getPixelFixer(draggable, i, j){
  let ai = 0;
  let onDraggableNodes = [];
  while(draggable[ai]){
    let aj = 0;
    while(draggable[ai][aj]){
      if(draggable[ai][aj] !== "0"){
        onDraggableNodes.push((ai * 2) + aj);
      }
      aj++;
    }
    ai++;
  }
  return onDraggableNodes;
}



