import React, { Component } from 'react';

class Tutorial extends Component {
  state = {
    isOpen: false
  }

  componentDidMount(){
    setTimeout(() => {
      this.setState({
        isOpen: true
      });
      setTimeout(() => {
        this.setState({
          isOpen: false
        });
      }, 3000);
    }, 4000);
  }

  render() {
    return (
      <div className={(this.state.isOpen)?"tutorial tOpen":"tutorial"}>
        <img alt="hand" style={{width: "20vw"}} src={require("./hand.png")}/>
      </div>
    );
  }
}

export default Tutorial;



