import React, { Component } from 'react';
import Draggable from 'react-draggable';
import './App.css';
import Board from './js/boardData';
import Event from './js/event';
import CONSTANTS from './js/constants';

import { getBorderStyle, getPixelFixer } from './tools/borderGenerator';
import makeid from './tools/makeid';
import { posToGrid } from './tools/gridPosConvertion';


const GAME_GRID = 8;

class DraggableItem extends Component {
  state = {
    placed: false,
    placeFade: true,
    placementPosition: {x: 0, y: 0},
    gridPlacementPosition: {x: -2, y: -2},
    highlight: []
  }

  componentWillMount(){
    this.boardOffset = 25;
    this.dCarrier = null;
    this.isTablet = false;
    this.fixedBoardWidth = false;

    this.gameWidth = window.innerWidth;
    this.gameHeight = window.innerHeight;

    if(this.gameWidth > 599) {
      this.isTablet = true;
      this.fixedBoardWidth = 500;
      if(this.gameWidth > 799){
        this.fixedBoardWidth = 700;
      }
    }

    if(this.isTablet){
      this.boardOffset = (this.gameWidth - this.fixedBoardWidth) / 2;
    }

    let boardWidth = 0;
    if(this.isTablet) {
      boardWidth = this.fixedBoardWidth;
    }else{
      boardWidth = (this.gameWidth - this.boardOffset * 2);
    }
    this.squareDimension = boardWidth / GAME_GRID;
    this.boardTopMargin = this.gameHeight * 0.25;

    this.startingTouchPosition = {windowX: null, windowY: null};
    this.startingOffsetPosition = {offsetX: null, offsetY: null};

    // Hold a private variable in order to keep the position of
    // draggable on the grid.
    this.setTo = [];
    this.props.item.forEach((setToInner) => {
      setToInner.forEach((s) => {
        this.setTo.push(0);
      });
    });

    // Listen events and update highlight if any changes happen
    Event.on("highlight", (highlightData) => {
      if(highlightData !== this.state.highlight){
        this.setState({
          highlight: highlightData
        });
      }
    })

  }

  clearSetToArray(){
    if(this.setTo[0] !== 0){
      // Clean the grid from the old position values
      // only if there is old position that exists.
      this.setTo.forEach((gridId) => {
        Board("update", {grid: gridId, reset: true});
      });
    }
  }

  checkPlacement(checkMap){
    this.clearSetToArray();
    let placeable = true;
    checkMap.forEach((item) => {
      if(Board("get", {grid: item.toGrid}) !== "0" && item.toLetter !== "0"){
        placeable = false;
      }
    });
    if(placeable){
      let iCM = 0;
      checkMap.forEach((item) => {
        this.setTo[iCM] = item.toGrid;
        Board("update", {grid: item.toGrid, value: item.toLetter});
        iCM++;
      });
    }
    return placeable;
  }

  handleStart = (e, data) => {
    e = e || window.event;
    var target = e.target || e.srcElement || e.originalTarget;
    let classNameOfTarget = target.className;
    if(classNameOfTarget.split(" ").contains("invisible") || classNameOfTarget.split(" ").contains("draggableNode")){
      return false;
    }

    if(!this.state.placed){
      this.startingOffsetPosition = {
        offsetX: data.node.getBoundingClientRect().left,
        offsetY: data.node.getBoundingClientRect().top
      };
    }
    if(e.changedTouches){
      let windowX = e.changedTouches[0].pageX;
      let windowY = e.changedTouches[0].pageY;
      this.startingTouchPosition = {windowX, windowY};
    }
    if(CONSTANTS.SOUND){
      window.postMessage("picked:sound");
    }
    return true;
  }

  handleStop = (e, data) => {
    let checkMap = [];
    let thisGrid = 0;
    let holdingX = 0;
    let holdingY = 0;
    let inValidPositioning = false;
    if(e.changedTouches && data){
      let holdLetter = null;
      if(e.srcElement){
        holdLetter = e.srcElement.getAttribute("data-letter");
        if(!holdLetter){
          holdLetter = e.srcElement.parentNode.getAttribute("data-letter");
          if(!holdLetter){
            inValidPositioning = true;
          }
        }
      }
      if(holdLetter){
        holdingX = e.changedTouches[0].pageX;
        holdingY = e.changedTouches[0].pageY - this.gameHeight * 0.09;
        let gridX = Math.ceil((holdingX - this.boardOffset) / this.squareDimension);
        let gridY = Math.ceil((holdingY - this.boardTopMargin) / this.squareDimension);
        let toGrid = (gridY - 1) * GAME_GRID + gridX;
        if(toGrid >= 1 && toGrid <= (GAME_GRID * GAME_GRID)){
          let pos_x = parseInt(e.srcElement.getAttribute("data-pos-x"), 10);
          let pos_y = parseInt(e.srcElement.getAttribute("data-pos-y"), 10);
          let max_x = parseInt(this.props.item[0].length, 10);
          let max_y = parseInt(this.props.item.length, 10);
          thisGrid = toGrid - pos_x * GAME_GRID - pos_y;

          // Which grid users cursor was
          if(max_x === 1 && max_y === 1){
            // X
            checkMap.push({toGrid: toGrid, toLetter: holdLetter});
          } else if(max_x === 1 && max_y === 2){
            // X
            // X
            if(pos_x === 0 && pos_y === 0){
              checkMap.push({toGrid: toGrid, toLetter: this.props.item[0][0]});
              checkMap.push({toGrid: toGrid + GAME_GRID, toLetter: this.props.item[1][0]});
            }else if(pos_x === 1 && pos_y === 0){
              checkMap.push({toGrid: toGrid - GAME_GRID, toLetter: this.props.item[0][0]});
              checkMap.push({toGrid: toGrid, toLetter: this.props.item[1][0]});
            }
          } else if(max_x === 2 && max_y === 1){
            // X X
            if(toGrid % GAME_GRID === 0 || (toGrid + 1) % GAME_GRID === 1){
              inValidPositioning = false;
            }
            if(pos_x === 0 && pos_y === 0){
              if((toGrid) % GAME_GRID !== 0){
                checkMap.push({toGrid: toGrid, toLetter: this.props.item[0][0]});
                checkMap.push({toGrid: toGrid + 1, toLetter: this.props.item[0][1]});
              }else{
                inValidPositioning = true;
              }
            }else if(pos_x === 0 && pos_y === 1){
              if((toGrid) % GAME_GRID !== 1){
                checkMap.push({toGrid: toGrid - 1, toLetter: this.props.item[0][0]});
                checkMap.push({toGrid: toGrid, toLetter: this.props.item[0][1]});
              }else{
                inValidPositioning = true;
              }
            }
          } else if(max_x === 2 && max_y === 2){
            // X X
            // X X
            if(pos_x === 0 && pos_y === 0){
              if(toGrid % GAME_GRID === 0 || (toGrid + 1) % GAME_GRID === 1){
                inValidPositioning = true;
              }
              checkMap.push({toGrid: toGrid, toLetter: this.props.item[0][0]});
              checkMap.push({toGrid: toGrid + 1, toLetter: this.props.item[0][1]});
              checkMap.push({toGrid: toGrid + GAME_GRID, toLetter: this.props.item[1][0]});
              checkMap.push({toGrid: toGrid + GAME_GRID + 1, toLetter: this.props.item[1][1]});
            }else if(pos_x === 1 && pos_y === 0){
              if((toGrid - GAME_GRID) % GAME_GRID === 0 || (toGrid - (GAME_GRID - 1)) % GAME_GRID === 1){
                inValidPositioning = true;
              }
              checkMap.push({toGrid: toGrid - GAME_GRID, toLetter: this.props.item[0][0]});
              checkMap.push({toGrid: toGrid - (GAME_GRID - 1), toLetter: this.props.item[0][1]});
              checkMap.push({toGrid: toGrid, toLetter: this.props.item[1][0]});
              checkMap.push({toGrid: toGrid + 1, toLetter: this.props.item[1][1]});
            }else if(pos_x === 0 && pos_y === 1){
              checkMap.push({toGrid: toGrid - 1, toLetter: this.props.item[0][0]});
              checkMap.push({toGrid: toGrid, toLetter: this.props.item[0][1]});
              checkMap.push({toGrid: toGrid + (GAME_GRID - 1), toLetter: this.props.item[1][0]});
              checkMap.push({toGrid: toGrid + GAME_GRID, toLetter: this.props.item[1][1]});
            }else if(pos_x === 1 && pos_y === 1){
              checkMap.push({toGrid: toGrid - (GAME_GRID + 1), toLetter: this.props.item[0][0]});
              checkMap.push({toGrid: toGrid - GAME_GRID, toLetter: this.props.item[0][1]});
              checkMap.push({toGrid: toGrid - 1, toLetter: this.props.item[1][0]});
              checkMap.push({toGrid: toGrid, toLetter: this.props.item[1][1]});
            }
          }
        }else{
          inValidPositioning = true;
        }
      }
    }
    let availableToCheck = true;
    let inValidGrid = false;
    if(!inValidPositioning){
      checkMap.forEach((gridPointer) => {
        if(!(gridPointer.toGrid >= 1 && gridPointer.toGrid <= (GAME_GRID * GAME_GRID))){
          inValidGrid = true;
        }
      });
      if(inValidGrid){
        availableToCheck = false;
      }
    }else{
      availableToCheck = false;
    }

    let availableToPlace = false;
    if(availableToCheck && this.checkPlacement(checkMap)){
      availableToPlace = true;
    }
    let placeGridX = ((thisGrid - 1) % GAME_GRID);
    let placeGridY = Math.floor(thisGrid / GAME_GRID);
    if(thisGrid % GAME_GRID === 0){
      placeGridY = placeGridY - 1;
    }

    if(availableToPlace && placeGridX >= 0 && placeGridX <= (GAME_GRID - 1) && placeGridY >= 0 && placeGridY <= (GAME_GRID - 1)){
      let jumpX = ((this.squareDimension) * placeGridX) + this.boardOffset;
      let jumpY = ((this.squareDimension) * placeGridY) + this.boardTopMargin;

      let translateString = this.dCarrier.style.transform;
      let translateX = translateString.split("px, ")[0].split("(")[1];
      let translateY = translateString.split(", ")[1].split("px)")[0];
      this.setState({
        placementPosition: {x: Number(translateX), y: Number(translateY - this.gameHeight * 0.09)}
      });

      translateString = this.dCarrier.style.transform;
      setTimeout(() => {
        this.setState({
          placed: true,
          placeFade: false,
          placementPosition: {x: jumpX - this.startingOffsetPosition.offsetX, y: jumpY - this.startingOffsetPosition.offsetY + 1},
          gridPlacementPosition: {x: placeGridX, y: placeGridY}
        });
        setTimeout(() => {
          this.setState({
            placeFade: true
          });
        }, 300);
      }, 50);

      if(CONSTANTS.SOUND){
        window.postMessage("placed:sound");
      }

    }else{
      this.clearSetToArray();
      this.setState({
        placed: false,
        placeFade: false,
        placementPosition: {x: 0, y: 0},
        gridPlacementPosition: {x: -2, y: -2}
      });
      setTimeout(() => {
        this.setState({
          placeFade: true
        });
      }, 300);
      if(CONSTANTS.SOUND){
        window.postMessage("notplaced:sound");
      }
    }
  };

  draggableNodeStyle(placeFade, highlight, gpp, i, j){
    let styleArray = ["draggableNode"];
    if(placeFade){
      styleArray.push("draggableNodeWithOutBorder");
      if(highlight.length){
        let highli = 0;
        highlight.forEach(highl => {
          if(highlight[highli].contains(posToGrid(gpp.x + j, gpp.y + i))){
            styleArray.push("highlighted" + (highli + 1));
          }
          highli++;
        });
      }
    }else{
      if(highlight.length){
        let highli = 0;
        highlight.forEach(highl => {
          if(highlight[highli].contains(posToGrid(gpp.x + j, gpp.y + i))){
            styleArray.push("highlighted" + (highli + 1));
          }
          highli++;
        });
      }
    }
    return styleArray.join(" ");
  }

  buildFixer(status, i, j, letter){
    let pixelArray = [];
    if(status.length === 1){
      // Dont push any pixels
    }else if(status.length === 2){
      if(status[1] === 1){
        // XX
        if((i*2 + j) === 0){
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 1){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
        }
      }else if(status[1] === 2){
        // X
        // X
        if((i*2 + j) === 0){
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 2){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
        }
      }
    }else if(status.length === 3){
      if(!status.contains(0)){
        if((i*2 + j) === 1){
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 2){
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 3){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
        }
      }else if(!status.contains(1)){
        if((i*2 + j) === 0){
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 2){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 3){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
        }
      }else if(!status.contains(2)){
        if((i*2 + j) === 0){
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 1){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
        }else if((i*2 + j) === 3){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
        }
      }else if(!status.contains(3)){
        if((i*2 + j) === 0){
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
          pixelArray.push(<div key={"br"} className="pixelFixer br"></div>);
        }else if((i*2 + j) === 1){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"bl"} className="pixelFixer bl"></div>);
        }else if((i*2 + j) === 2){
          pixelArray.push(<div key={"tl"} className="pixelFixer tl"></div>);
          pixelArray.push(<div key={"tr"} className="pixelFixer tr"></div>);
        }
      }
    }else if(status.length === 4){
      // XX
      // XX
      // Don't Push any pixels
    }

    return (
      <div className="pixelFixerCarrier" data-pos-x={i} data-pos-y={j} data-letter={letter}>
      {pixelArray}
      </div>
    );
  }

  renderDraggableNodes(placeFade, gpp){
    let draggableNodes = [];
    let i = 0;

    this.props.item.forEach((letters) => {
      let j = 0;
      letters.forEach((letter) => {
        if(letter === "0"){
          draggableNodes.push(
            <div
              key={makeid()}
              data-pos-x={i}
              data-pos-y={j}
              className="draggableNode invisible">
            </div>
          );
        }else{
          draggableNodes.push(
            <div
              key={makeid()}
              className={this.draggableNodeStyle(placeFade, this.state.highlight, gpp, i, j)}
              style={getBorderStyle(this.props.item, i, j)}>
              {letter}
              {this.buildFixer(getPixelFixer(this.props.item, i, j), i, j, letter)}
              </div>
          );
        }
        j++;
      });
      draggableNodes.push(<div key={makeid()} className="clear"></div>);
      i++;
    });
    return draggableNodes;
  }

  render() {
    return (
      <Draggable
        onStart={this.handleStart}
        onStop={this.handleStop}
        position={this.state.placementPosition}>
        <div className={(this.state.placeFade)?"draggableNodeCarrier":"draggableNodeCarrier nodeFading"}
             ref={(dCarrier) => { this.dCarrier = dCarrier; }}>
          {this.renderDraggableNodes(this.state.placeFade, this.state.gridPlacementPosition)}
        </div>
      </Draggable>
    );
  }
}

export default DraggableItem;



