import React, { Component } from 'react';
//import Event from './js/event';
import CONSTANTS from './js/constants';

class Audio extends Component {
  componentDidMount(){

    // Preload for some browsers.
    // and set volumes
    this.refs.picked.load();
    this.refs.picked.volume = CONSTANTS.GAME_VOLUME;
    this.refs.placed.load();
    this.refs.placed.volume = CONSTANTS.GAME_VOLUME;
    this.refs.notplaced.load();
    this.refs.notplaced.volume = CONSTANTS.GAME_VOLUME;

    /*
    Event.on("sound", (data) => {
      this.refs[data].pause();
      this.refs[data].currentTime = 0
      this.refs[data].play();
    });
    */
  }

  render() {
    return (
      <div style={{position: "fixed", top: -300, left: -300}}>
        <audio ref="picked">
          <source src={require("./sound/tik.wav")} preload='auto'/>
        </audio>
        <audio ref="placed">
          <source src={require("./sound/pbic.wav")} preload='auto'/>
        </audio>
        <audio ref="notplaced">
          <source src={require("./sound/tuik.wav")} preload='auto'/>
        </audio>
      </div>
    );
  }
}

export default Audio;



