import CONSTANTS from '../js/constants';

export function gridToPos(grid){
  return {
    x: Math.floor((grid - 1)/CONSTANTS.GAME_GRID),
    y: (grid - 1)%CONSTANTS.GAME_GRID
  }
}

export function posToGrid(x, y){
  return CONSTANTS.GAME_GRID * y + (x + 1);
}



