import React, { Component } from 'react';
import './App.css';
import {posToGrid} from './tools/gridPosConvertion';
import Event from './js/event';

class Game extends Component {
  state = {
    highlight: []
  }

  componentWillMount(){
    this.boardData = JSON.parse(JSON.stringify(this.props.boardData));
    Event.on("highlight", (highlightData) => {
      if(highlightData !== this.state.highlight){
        this.setState({
          highlight: highlightData
        });
      }
    });
  }

  componentDidMount(){
    window.postMessage(":gameReady", "*");
  }

  isHighlighted(highlights, i, j){
    let styleArray = ["node"];
    if(this.boardData[j][i] === "0"){
      styleArray.push("nodeEmpty");
    }else if(this.boardData[j][i] === "*"){
      styleArray.push("nodeLocked");
    }else{
      let highli = 0;
      highlights.forEach(highl => {
        if(highlights[highli].contains(posToGrid(i,j))){
          styleArray.push("highlighted" + (highli+1));
        }
        highli++;
      });
    }
    return styleArray.join(" ");
  }

  getNodes(xlen, ylen){
    let i = 0;
    let nodes = [];
    while(i < xlen){
      let j = 0;
      while(j < ylen){
        nodes.push(
          <div className={this.isHighlighted(this.state.highlight, i, j)} key={i+"x"+j}>
            {(this.boardData[j][i] !== "0" && this.boardData[j][i] !== "*")?this.boardData[j][i]:""}
          </div>
        );
        j++;
      }
      i++;
    }
    return nodes;
  }

  render() {
    return (
      <div className="Game GameStarted">
      {this.getNodes(this.props.board, this.props.board)}
      </div>
    );
  }
}

export default Game;



