import {gridToPos, posToGrid} from '../tools/gridPosConvertion';
import Event from '../js/event';
import CONSTANTS from '../js/constants';

let initialBoard = [];
let boardData = [];
let words = [];
let highlightPos = [];
let wordProgress = [];

export default function Board(type, data){
  if(type === "get"){
    if(!data){
      return boardData;
      // returns the current boardData if no data supplied
    }else if(data.x && data.y){
      return boardData[data.x][data.y];
      // returns the pointed board tile value
      // data: {x: .., y, ..}
    }else if(data.grid){
      return boardData[gridToPos(data.grid).x][gridToPos(data.grid).y];
      // data: {grid: ..}
    }
  }else if(type === "set"){
    initialBoard = JSON.parse(JSON.stringify(data.board));
    boardData = data.board;
    words = data.words;
    return true;
    // data supplied for set must be an array
  }else if(type === "update"){
    if(data.x && data.y){
      if(!data.reset){
        boardData[data.x][data.y] = data.value;
      }else{
        boardData[data.x][data.y] = initialBoard[data.x][data.y];
      }
    }else if(data.grid){
      if(!data.reset){
        if(boardData[gridToPos(data.grid).x][gridToPos(data.grid).y] === "0"){
          boardData[gridToPos(data.grid).x][gridToPos(data.grid).y] = data.value;
        }
      }else{
        boardData[gridToPos(data.grid).x][gridToPos(data.grid).y] = initialBoard[gridToPos(data.grid).x][gridToPos(data.grid).y];
      }
    }
    checkAndUpdateHighlights(boardData);
    return true;
    // data supplied for update must be an obj
    // data: {x: .., y: .., value: ..}
  }else if(type === "hint"){
    let i = 0;
    while(words[i]){
      if(!wordProgress.contains(words[i])){
        return words[i][0];
      }
      i++;
    }
  }
}

function checkAndUpdateHighlights(currentBoardData){
  // Use event with highlight key for new changes.
  // use highlightCache :/
  let highlightCache = [];
  let wordProgressCache = [];
  words.forEach(word => {
    highlightCache.push([]);
  });

  let wordIndis = 0;
  words.forEach(word => {
    let i = 0;
    while(i < CONSTANTS.GAME_GRID){
      let j = 0;
      while(j < CONSTANTS.GAME_GRID){
        if(currentBoardData[j][i] === word[0]){
          if(CONSTANTS.GAME_GRID - i >= word.length){
            let isSet = false;
            let wi = 0;
            while(wi < word.length){
            //console.log(currentBoardData[j][i + wi] + " - " + word[wi]);
              if(currentBoardData[j][i + wi]){
                if(!(currentBoardData[j][i + wi] === word[wi])){
                  //console.log("fail");
                  break;
                }else{
                  if(wi === word.length - 1){
                    isSet = true;
                  }
                }
              }
              wi++;
            }
            if(isSet){
              //console.log("isset");
              wordProgressCache = uniqueWordPush(wordProgressCache, word);
              let wi = 0;
              while(wi < word.length){
                highlightCache[wordIndis].push(posToGrid(i + wi, j));
                wi++;
              }
            }
          }
          if(CONSTANTS.GAME_GRID - j >= word.length){
            let isSet = false;
            let wj = 0;

            while(wj < word.length){
              //console.log(currentBoardData[j + wj][i] + " - " + word[wj]);
              if(currentBoardData[j + wj][i]){
                if(!(currentBoardData[j + wj][i] === word[wj])){
                  //console.log("fail");
                  break;
                }else{
                  if(wj === word.length - 1){
                    isSet = true;
                  }
                }
              }
              wj++;
            }
            if(isSet){
              //console.log("isset");
              wordProgressCache = uniqueWordPush(wordProgressCache, word);
              let wj = 0;
              while(wj < word.length){
                highlightCache[wordIndis].push(posToGrid(i, j + wj));
                wj++;
              }
            }
          }
        }
        j++;
      }
      i++;
    }
    wordIndis++;
  });
  if(JSON.stringify(highlightCache) !== JSON.stringify(highlightPos)){
    let hcLength = (highlightPos.length);
    highlightPos = highlightCache;
    if(hcLength){
      Event.emit("highlight", highlightPos);
      if(CONSTANTS.SOUND){
        window.postMessage("success:sound");
      }
      console.log("successs!!!!");
    }
  }
  if(JSON.stringify(wordProgressCache) !== JSON.stringify(wordProgress)){
    wordProgress = wordProgressCache;
    Event.emit("progress", wordProgress);
  }
}

function uniqueWordPush(arr, stringToPush){
  if(!arr.contains(stringToPush)){
    arr.push(stringToPush);
  }
  return arr;
}



